.input {
  padding: 0;
  margin: 10px 0 20px;
  background-color: #2f2f2f;
  border: 0;
  width: 100%;
  height: 46px;
  border-radius: 8px;
  text-indent: 15px;
  color: #fff;
  outline: none;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

@media screen and (max-width: 395px){
  .input {
    width: 260px;
    display: flex;
  }
}
